//======> Tornado UI Core Base <=======//
import './Base/Tornado';
import Tornado from './Base/Tornado';

/*=== Theme Customize ===*/
document.addEventListener('DOMContentLoaded', function () {
    'use strict';

    //====== Media Carousel ======//
    Tornado.slider('.featured-slider',{
        items: 1,
        uniuqClass : 'featured-slider-outer',
        responsive: {
            medium : {items:3},
            small : {items:2},
        }
    });

    //====== Media Carousel ======//
    Tornado.slider('.media-carousel',{
        items: 1,
        uniuqClass : 'media-slider-outer',
        responsive: {
            large  : {items:4},
            medium : {items:3},
            small  : {items:2},
        }
    });

    //====== Related Carousel ======//
    Tornado.slider('.related-media',{
        items: 1,
        uniuqClass : 'related-slider-outer',
        responsive: {
            medium  : {items:3},
            small  : {items:2},
        }
    });

    /*=====> Player List Height <=====*/
    var playerEl = Tornado.getElement('.media-player .player');
    if (playerEl) var PlayerHeight = Tornado.getHeight(playerEl);

    /*===== Fake Scroll =====*/
    var fakeScroollElement = Tornado.getElement('.fake-scroll');
    if (fakeScroollElement) {
        fakeScroollElement.style.height = PlayerHeight+'px';
        fakeScroollElement.fakeScroll();
    }

    /*====== Manga Reader Full Screen ======*/
    var fullScreenBtn = Tornado.getElement('.reader-wraper .reader-fs');
    if (fullScreenBtn) {
        fullScreenBtn.addEventListener('click', event => {
            event.preventDefault();
            var ReaderWraper = Tornado.parentsUntil (fullScreenBtn, '.reader-wraper'),
                docElement = document.documentElement;
            ReaderWraper.classList.toggle('full-screen');
            if(ReaderWraper.classList.contains('full-screen')) {
                openFullscreen(docElement);
            } else {
                closeFullscreen(document);
            }
        });
    }

    /*======> Manga Reader Navigation <==========*/
    var nextPage = Tornado.getElements('.reader-navigation .next-btn'),
        prevPage = Tornado.getElements('.reader-navigation .prev-btn'),
        readerPager = Tornado.getElements('.reader-navigation .reader-pager'),
        readerList = Tornado.getElement('.reader-list'),
        currenDir = Tornado.direction('page');

    /*====> Next Page <=====*/
    nextPage.forEach(element => {
        element.addEventListener('click', event => {
            event.preventDefault();
            /*======> Page Data <======*/
            var currentPage = readerList.getAttribute('data-current'),
                pageNumb = parseInt(currentPage)+1,
                pageUrl = readerList.getAttribute(`data-page-${pageNumb}`);
            /*======> Page Change <======*/
            if (pageUrl) {
                readerList.setAttribute('data-current', pageNumb);
                readerList.querySelector('img').setAttribute('src',pageUrl);
                /*======> Pager Change <======*/
                readerPager.forEach(element => {
                    if(currenDir == 'ltr') element.textContent = 'Page 0'+pageNumb;
                    if(currenDir == 'rtl') element.textContent = 'الصفحة 0'+pageNumb;
                });
            }
        });
    });

    /*====> Prev Page <=====*/
    prevPage.forEach(element => {
        element.addEventListener('click', event => {
            event.preventDefault();
            /*======> Page Data <======*/
            var currentPage = readerList.getAttribute('data-current'),
                pageNumb = parseInt(currentPage)-1,
                pageUrl = readerList.getAttribute(`data-page-${pageNumb}`);
            /*======> Page Change <======*/
            if (pageUrl) {
                readerList.setAttribute('data-current', pageNumb);
                readerList.querySelector('img').setAttribute('src',pageUrl);
                /*======> Pager Change <======*/
                readerPager.forEach(element => {
                    if(currenDir == 'ltr') element.textContent = 'Page 0'+pageNumb;
                    if(currenDir == 'rtl') element.textContent = 'الصفحة 0'+pageNumb;
                });
            }
        });
    });

    /* View in fullscreen */
    function openFullscreen(elem) {
        if (elem.requestFullscreen) {
            elem.requestFullscreen();
        } else if (elem.mozRequestFullScreen) {
            /* Firefox */
            elem.mozRequestFullScreen();
        } else if (elem.webkitRequestFullscreen) {
            /* Chrome, Safari and Opera */
            elem.webkitRequestFullscreen();
        } else if (elem.msRequestFullscreen) {
            /* IE/Edge */
            elem.msRequestFullscreen();
        }
    }

    /* Close fullscreen */
    function closeFullscreen(elem) {
        if (elem.exitFullscreen) {
            elem.exitFullscreen();
        } else if (elem.mozCancelFullScreen) {
            /* Firefox */
            elem.mozCancelFullScreen();
        } else if (elem.webkitExitFullscreen) {
            /* Chrome, Safari and Opera */
            elem.webkitExitFullscreen();
        } else if (elem.msExitFullscreen) {
            /* IE/Edge */
            elem.msExitFullscreen();
        }
    }

    /*=====> Advanced Search <=====*/
    var advSearch = Tornado.getElement('.advanced-search .search-title');
    if(advSearch) {
        advSearch.addEventListener('click', element => {
            var searchBox = Tornado.getElement('.advanced-search-form');
            if(searchBox) Tornado.slideToggle(searchBox,300);
        });
    }
});


